import React, { memo, useRef, useEffect, useState } from "react";
import { viewportCoordsToSceneCoords, rgb } from "@/util";
import MarkDown from "@/components/markdown";
import doc from "@doc/canvasËøõÈò∂/ÁÇπÁ®ÄÈáä.md";
import "./index.less";
import renderScene, {
  deleteElementCache,
  renderDraggingScene,
} from "./renderScene";
import { withBatchedUpdatesThrottled, setCanvasSize } from "./util";
export let elements =
  JSON.parse(localStorage.getItem("free-draw-elements")) || [];
const appState = {
  offsetLeft: 0,
  offsetTop: 0,
  scrollX: 0,
  scrollY: 0,
  draggingElement: null,
};
const Canvas = memo(() => {
  const canvasRef = useRef(null);
  const canvasContainer = useRef(null);
  const staticCanvasRef = useRef(null);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    // canvasÂàÜËæ®ÁéáÁü´Ê≠£
    const canvas = canvasRef.current;
    const { offsetWidth, offsetHeight } = canvas;
    setCanvasSize(canvas, offsetWidth, offsetHeight);
    setCanvasSize(staticCanvasRef.current, offsetWidth, offsetHeight);
    // ËÆæÁΩÆappState
    const { x, y } = canvas.getBoundingClientRect();
    // const offsetTop = Math.ceil(y);
    // const offsetLeft = Math.ceil(x);

    appState.offsetLeft = x; //offsetLeft;
    appState.offsetTop = y; //offsetTop;
    // ÁªòÂà∂ÈùôÊÄÅcanvas
    renderScene(canvasContainer.current, staticCanvasRef.current, appState);

    // Èò≤Ê≠¢ÂèåÊåáÊªëÂä®Êó∂ÂàáÊç¢È°µÈù¢
    const wrap = canvasContainer.current;
    const handleWheel = (e) => {
      e.preventDefault();
    };
    // Èò≤Ê≠¢ÂèåÊåáÊªëÂä®Êó∂ÂàáÊç¢È°µÈù¢
    wrap.addEventListener("wheel", handleWheel, {
      passive: false,
    });
    return () => {
      wrap.removeEventListener("wheel", handleWheel);
    };
  }, []);
  useEffect(() => {
    import("../multiple-layered/1500").then((res) => {
    });
  }, []);
  const handleCanvasPointerDown = (event) => {
    const origin = viewportCoordsToSceneCoords(event, appState);

    const pointerDownState = {
      origin,
      lastCoords: { ...origin },
      eventListeners: {
        onMove: null,
        onUp: null,
      },
    };
    const element = {
      x: pointerDownState.origin.x,
      y: pointerDownState.origin.y,
      points: [[pointerDownState.origin.x, pointerDownState.origin.y]],
      strokeColor: "#000000",
      backgroundColor: "transparent",
      fillStyle: "hachure",
      strokeWidth: 1,
      strokeStyle: rgb(),
    };
    appState.draggingElement = element;
    deleteElementCache(element);
    // elements.push(element);

    const onPointerMove =
      onPointerMoveFromCanvasPointerDownHandler(pointerDownState);
    const onPointerUp =
      onPointerUpFromCanvasPointerDownHandler(pointerDownState);
    window.addEventListener("pointermove", onPointerMove);
    window.addEventListener("pointerup", onPointerUp);
    pointerDownState.eventListeners.onMove = onPointerMove;
    pointerDownState.eventListeners.onUp = onPointerUp;
  };
  const onPointerMoveFromCanvasPointerDownHandler = (pointerDownState) =>
    withBatchedUpdatesThrottled((event) => {
      const pointerCoords = viewportCoordsToSceneCoords(event, appState);
      appState.draggingElement.points.push([pointerCoords.x, pointerCoords.y]);

      renderDraggingScene(canvasRef.current, appState);
    });

  const onPointerUpFromCanvasPointerDownHandler = (pointerDownState) => () => {
    deleteElementCache(appState.draggingElement);
    elements.push(appState.draggingElement);
    const canvas = canvasRef.current;
    const context = canvas.getContext("2d");
    context.clearRect(0, 0, canvas.width, canvas.height);
    renderScene(canvasContainer.current, staticCanvasRef.current, appState);
    window.removeEventListener(
      "pointermove",
      pointerDownState.eventListeners.onMove
    );
    window.removeEventListener(
      "pointerup",
      pointerDownState.eventListeners.onUp
    );
  };
  return (
    <div className="dynamic-layer">
      <div className="container wrap" ref={canvasContainer}>
        <canvas ref={staticCanvasRef} className="canvas">
          ÈùôÊÄÅcanvas
        </canvas>
        <canvas
          ref={canvasRef}
          className="canvas"
          onPointerDown={handleCanvasPointerDown}
        >
          Âä®ÊÄÅcanvas
        </canvas>
        <button
          className="btn clear"
          onClick={() => {
            localStorage.setItem("free-draw-elements", JSON.stringify([]));
            elements = [];
            // ÁªòÂà∂ÈùôÊÄÅcanvas
            renderScene(canvasContainer.current, staticCanvasRef.current, appState);
          }}
        >
          Ê∏ÖÁ©∫localstorage
        </button>
        <button
          className="btn"
          onClick={() => {
            if (loading) return;
            setLoading(true);
            import("../multiple-layered/1500")
              .then((res) => {
                const largeData = res.default;
                localStorage.setItem(
                  "free-draw-elements",
                  JSON.stringify(largeData)
                );
                elements = largeData;
                // ÁªòÂà∂ÈùôÊÄÅcanvas
                renderScene(canvasContainer.current, staticCanvasRef.current, appState);
              })
              .finally(() => {
                setLoading(false);
              });
          }}
        >
          ÊûÅÈôêÊµãËØï
        </button>
      </div>
      {/* <div id="offscreen"></div> */}
      <MarkDown src={doc} />
    </div>
  );
});

export default Canvas;
